import React, { Component } from 'react'
import $ from 'jquery'
import url from './audio/100000hours.mp3' // 引入背景音乐文件


class Main extends Component {
    state = {
        date: {},
    }
    componentDidMount() {
        this.print();
        setInterval(() => {
            this.time(2019, 8, 5) // 你们的纪念日
        }, 1000
        )
        var audio = document.getElementById("audio");
        setTimeout(() => audio.play(), 8500) // 背景音乐在页面加载后，延迟播放的时长，单位：毫秒。
    }
    print = () => {
        $.fn.autotype = function () {
            var _this = $(this);
            var str = _this.html();
            // 正则替换代码行之间添加的多个空格，不去除换行输出会有明显的停顿：实际是在输出多个空格
            str = str.replace(/(\s){2,}/g, "$1");
            var index = 0;
            $(this).html('');
            var timer = function fn() {
                var args = arguments;
                var current = str.slice(index, index + 1);
                // html标签完整输出,如：<p>
                if (current == '<') {
                    index = str.indexOf('>', index) + 1;
                }
                else {
                    index++;
                }
                //位运算符: 根据setInterval运行奇偶次来判断是否加入下划线字符“_”，使输入效果更逼真
                if (index < str.length - 1) { //打印字符倒数第2个字符开始，不加下划线字符，以防止结束符可能会多输出一下划线字符
                    _this.html(str.substring(0, index) + (index & 1 ? '_' : ''));
                } else {
                    _this.html(str.substring(0, index));
                    clearTimeout(timer);
                };
                setTimeout(fn, 200)
            };
            // 延迟1s开始
            setTimeout(timer, 1000);
        };
        $("#autotype").autotype();
    }
    time = (year, month, day) => {
        var dateNow = new Date();
        var dateJNR = new Date(year, month - 1, day);
        // var anniversary = parseInt((dateNow - dateJNR) / (365*24*3600*1000))
        var d = parseInt((dateNow - dateJNR) / (24 * 3600 * 1000));
        var hour = parseInt(((dateNow - dateJNR) / (3600 * 1000)) % 24);
        var minute = parseInt((dateNow - dateJNR) / (1000 * 60) % 60);
        var second = parseInt((dateNow - dateJNR) / 1000 % 60);
        this.setState({ date: { d: d, hour: hour, minute: minute, second: second } });
    };
    render() {
        const date = () => {
            if (this.state.date.d !== undefined) {
                const { d, hour, minute, second } = this.state.date
                return (<p>我们已经一起走过了: <span className="date-text">{d}</span> 天 <span className="date-text">{hour}</span> 小时 <span className="date-text">{minute}</span> 分 <span className="date-text">{second}</span> 秒 </p>
                )
            }
        }
        return (
            <div className="App animated bounceInLeft">
                <div className="date">{date()}</div>
                <div id="autotype">
                    <h1 style={{ fontWeight: 900 }}>嗨嗨！我的小美女！</h1>
                    <p >我要开始煽情了，此刻怎么能少了背景音乐！Music!</p>
                    <p>今天是我们在一起的第二个情人节，很遗憾七夕和今天我都不在你身边，对不起，只有这些文字能够跨越距离出现
                    在你面前表达我的情绪。虽然聚少离多，但我们一起度过的每一天都很开心。</p>
                    <p>本来说要给你一个新年礼物，我没有忘，只是过年的时候我还没做好，没想到等到了现在。跟你说个秘密: 
                        我真的喜欢你很久啦，但是年轻的时候的少男少女心思都太多了，可能当时你的心思在别人身上并没有
                        注意到我。你吐槽我跟你出去玩的时候不把你送回家，其实我真没送过女生回家哈哈哈哈，我以后一定改，
                        我会跟你一起回家。还记得我们高中毕业的时候还合过照嘞，不过我还没有看到过这张照片哈哈哈哈。
                </p>
                    <p>2019年7月6号，我胆子超大的，回想一下你妈你哥还有你妹这豪华阵容，我当时居然就傻愣愣的直接冲过去了。那一天我其实很紧张，
                        尤其是牵你的手你还把我甩开的时候，记得在看音乐喷泉时我那无处安放的小手吗。后来我问你，你说: 谁知道你就不牵了，女生
                        的千层套路真是让人迷惑。不过如果再给我一次机会，我还是会做同样的事，有时候未经思考去做的事是我一直想却又不敢做的事。很幸运最后能和你在一起，遇见你是我最大的幸运。
                </p>
                    <p>8月9号，那晚的风儿甚是喧嚣。很幸运我乘坐的高铁没有因为台风停运，台风来的时候我就在你的身边陪你，没有让你一个人。一直以来，有很多事情我都做的不对
                        做的不好，让你受了很多委屈，你容易哭我却总是让你哭，你真的很好每次都能理解我，不过我也会改进改进再改进的。（悄悄跟你说，你笑起来真的超级
                        好看的，我很喜欢嘻嘻）以后我们还要经历很多的事，但是我会一直在你身边，跟你一起面对。
                </p>
                    <p>我不善于文字表达，写下来可能还有点语句不通顺，我想表达的就是 “我喜欢你，过去、现在、未来”。
                        希望早点能见到你，么么哒。
                </p>
                    <p>最后我家小丽瑶情人节要快乐！</p>
                    <div style={{ textAlign: 'right' }}>
                        <p>爱你的爸爸</p>
                        <p>2020年2月14日</p>
                    </div>
                </div>
                <audio id="audio" src={url}></audio>
            </div>

        )
    }
}
export default Main


